:root {
  --link-color: #035fe6;
  --link-hover-color: #136ff6;
  --background-color: #fff;
  --overlay-background-color: #eee;
  --highlight-background-color: #ccc;
  --text-color: #000;
  --body-font-family: roboto, roboto-fallback;
  --heading-font-family: var(--body-font-family);
  --fixed-font-family: "Roboto Mono", menlo, consolas, "Liberation Mono", monospace;
  --body-font-size-m: 22px;
  --body-font-size-s: 18px;
  --body-font-size-xs: 16px;
  --heading-font-size-xxl: 48px;
  --heading-font-size-xl: 40px;
  --heading-font-size-l: 32px;
  --heading-font-size-m: 24px;
  --heading-font-size-s: 20px;
  --heading-font-size-xs: 18px;
  --nav-height: 64px;
}

@font-face {
  font-family: roboto-fallback;
  size-adjust: 100.06%;
  ascent-override: 95%;
  src: local(Arial);
}

@media (min-width: 900px) {
  :root {
    --heading-font-size-xxl: 60px;
    --heading-font-size-xl: 48px;
    --heading-font-size-l: 36px;
    --heading-font-size-m: 30px;
    --heading-font-size-s: 24px;
    --heading-font-size-xs: 22px;
  }
}

body {
  font-size: var(--body-font-size-m);
  font-family: var(--body-font-family);
  color: var(--text-color);
  background-color: var(--background-color);
  margin: 0;
  line-height: 1.6;
}

header {
  height: var(--nav-height);
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--heading-font-family);
  scroll-margin: calc(var(--nav-height)  + 1em);
  margin-top: 1em;
  margin-bottom: .5em;
  font-weight: 600;
  line-height: 1.25;
}

h1 {
  font-size: var(--heading-font-size-xxl);
}

h2 {
  font-size: var(--heading-font-size-xl);
}

h3 {
  font-size: var(--heading-font-size-l);
}

h4 {
  font-size: var(--heading-font-size-m);
}

h5 {
  font-size: var(--heading-font-size-s);
}

h6 {
  font-size: var(--heading-font-size-xs);
}

p, dl, ol, ul, pre, blockquote {
  margin-top: 1em;
  margin-bottom: 1em;
}

hr {
  border: 0;
  border-bottom: 1px solid var(--overlay-background-color);
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

code, pre, samp {
  font-family: var(--fixed-font-family);
  font-size: var(--body-font-size-s);
}

code, samp {
  padding: .125em;
}

pre {
  overflow: scroll;
}

main pre {
  background-color: var(--overlay-background-color);
  white-space: pre;
  border-radius: .25em;
  padding: 1em;
  overflow-x: auto;
}

a:any-link {
  color: var(--link-color);
  text-decoration: none;
}

a:hover {
  color: var(--link-hover-color);
  text-decoration: underline;
}

a.button:any-link, button {
  font-family: var(--body-font-family);
  box-sizing: border-box;
  text-align: center;
  cursor: pointer;
  color: var(--background-color);
  background-color: var(--link-color);
  white-space: nowrap;
  text-overflow: ellipsis;
  border: 2px solid #0000;
  border-radius: 30px;
  margin: 16px 0;
  padding: 5px 30px;
  font-style: normal;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
  overflow: hidden;
}

a.button:hover, a.button:focus, button:hover, button:focus {
  background-color: var(--link-hover-color);
  cursor: pointer;
}

button:disabled, button:disabled:hover {
  background-color: var(--overlay-background-color);
  cursor: unset;
}

a.button.secondary, button.secondary {
  background-color: unset;
  color: var(--text-color);
  border: 2px solid;
}

main input {
  box-sizing: border-box;
  border: 1px solid var(--text-color);
  color: var(--text-color);
  background-color: var(--background-color);
  border-radius: .25rem;
  width: 100%;
  max-width: 50rem;
  margin-bottom: 1rem;
  padding: .75rem .6rem;
  font-size: 1.25rem;
  display: block;
}

main input:hover {
  border: 1px solid var(--text-color);
}

main blockquote {
  text-indent: -1rem;
  hanging-punctuation: first;
  margin: 3rem;
  font-style: italic;
}

main blockquote p:before {
  content: "“";
  line-height: 0;
}

main blockquote p:after {
  content: "”";
  line-height: 0;
}

main img {
  width: auto;
  max-width: 100%;
  height: auto;
}

.icon {
  width: 24px;
  height: 24px;
  display: inline-block;
}

.icon img {
  width: 100%;
  height: 100%;
}

@media (min-width: 900px) {
  .section > * {
    max-width: 1200px;
    margin: auto;
  }

  .section > p, .section > h2 {
    margin-bottom: 32px;
  }
}

main > div {
  padding: 64px 16px;
}

@media (min-width: 600px) {
  main > div {
    padding: 64px 32px;
  }
}

@media (min-width: 900px) {
  main > div > div:not(.section), main > div:not(.hero) > * {
    max-width: 1200px;
    margin: auto;
  }
}

main > .section.highlight {
  background-color: var(--highlight-background-color);
}

.section-metadata {
  display: none;
}

footer {
  background-color: var(--overlay-background-color);
  font-size: var(--body-font-size-s);
  height: 85px;
  padding: 2rem;
}

footer:not(:scope > .footer) {
  height: unset;
}

footer .footer {
  max-width: 1200px;
  margin: auto;
}

footer .footer p {
  margin: 0;
}

header {
  height: var(--nav-height);
  background-color: #f5f5f5;
}

nav {
  box-sizing: border-box;
  height: var(--nav-height);
  height: var(--nav-height);
  font-family: var(--body-font-family);
  font-size: var(--body-font-size-xs);
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 1264px;
  margin: auto;
  padding: 0 1rem;
  display: flex;
}

nav div:first-of-type a {
  font-size: var(--body-font-size-m);
  color: #000;
}

nav ul {
  list-style: none;
}

nav > div > ul {
  flex-direction: row;
  align-items: center;
  display: flex;
}

nav > div > ul > li {
  cursor: pointer;
  margin-left: 16px;
  transition-duration: .2s;
  position: relative;
}

nav > div > ul > li:hover {
  color: gray;
  text-decoration: underline;
  display: inline;
}

nav ul li ul {
  z-index: 1;
  background: #fff;
  border: 1px solid #000;
  margin: 0;
  padding: 0;
  display: none;
  position: absolute;
  top: 100%;
  left: 0%;
}

nav ul > li > ul > li {
  min-width: 200px;
  padding: 2px 16px;
}

nav ul li:hover ul {
  display: unset;
}

.icon-search {
  cursor: pointer;
  aspect-ratio: 1;
}

.cards > ul {
  grid-gap: 16px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.cards > ul > li {
  border: 1px solid var(--highlight-background-color);
  background-color: var(--background-color);
}

.cards .cards-card-body {
  margin: 16px;
}

.cards .cards-card-image {
  line-height: 0;
}

.cards .cards-card-body > :first-child {
  margin-top: 0;
}

.cards > ul > li img {
  aspect-ratio: 4 / 3;
  object-fit: cover;
  width: 100%;
}

.cards:not(.block) {
  grid-gap: 16px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.cards.block {
  grid-gap: unset;
  display: block;
  grid-template-columns: unset !important;
}

.cards:not([data-rendered]) > div > div img {
  aspect-ratio: 4 / 3;
  object-fit: cover;
  width: 100%;
}

.hero {
  aspect-ratio: 9 / 16;
  min-height: 300px;
  padding: 32px;
  position: relative;
}

main > div:has(h1):first-of-type {
  aspect-ratio: 9 / 16;
  min-height: 300px;
  padding: 32px;
  position: relative;
}

main > div:has(h1):first-of-type {
  display: inline;
  overflow: hidden;
}

.section {
  max-width: 1200px;
  margin: auto;
}

.hero-wrapper {
  aspect-ratio: unset !important;
  display: block !important;
}

main > div > div.hero {
  aspect-ratio: unset !important;
}

main .hero h1, main p + h1 {
  color: #fff;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

main p + h1 {
  position: absolute;
  top: 0%;
}

main .hero picture, main > p picture {
  z-index: -1;
  object-fit: cover;
  box-sizing: border-box;
  aspect-ratio: 16 / 9;
  position: absolute;
  inset: 0;
}

main .hero img, main > p img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.columns > div {
  flex-direction: column;
  display: flex;
}

.columns img {
  width: 100%;
}

.columns > div > div {
  order: 1;
}

.columns > div > .columns-img-col {
  order: 0;
}

.columns > div > .columns-img-col img {
  display: block;
}

@media (min-width: 900px) {
  .columns > div {
    flex-direction: unset;
    align-items: center;
    gap: 32px;
  }

  .columns > div > div {
    flex: 1;
    order: unset;
  }
}

